import React, { Component } from 'react';
import {Layout} from 'antd'
import AddButton from '../images/add-button.png'
import QuestionList from './questionlist'
import {updateQuestions} from '../actions/createAction'
import {connect} from 'react-redux'
class Question extends Component {
  addQuestion(){
    console.log("question add")
    let questions = [...this.props.questions];
    let questionTemplate = {
      question:"Type Something ...",
      option:["Option 1"]
    }
    questions.push(questionTemplate);
    this.props.updateQuestions(questions);
  }
  render() {
    console.log(this.props.questions)
    return (
      <div className="create-question-container padding-30">
          <div style={{maxHeight:'90%',overflowX:'scroll',padding:5}}>
          <QuestionList list={this.props.questions} />
          </div>
          <img src={AddButton} alt="Add Button" className="add-question-button" onClick={()=>this.addQuestion()}/>
      </div>
    );
  }
}
const mapDispatchToProps = {
  updateQuestions
}
const mapStateToProps = (state) => ({
  questions: state.create.questions
})

export default connect(mapStateToProps,mapDispatchToProps)(Question);
