import React, { Component } from 'react';
import {Layout,Row, Col,Input,Icon} from 'antd'
import CopyDelete from '../images/copy.png'
import Delete from '../images/delete.png'
import {connect} from 'react-redux'
import {updateQuestion,copyQuestion,deleteQuestion} from '../actions/createAction';
class QuestionItem extends Component {
  updateQuestion = (event) => {
    let {index}=this.props;
    //console.log(this.props.index);
    let questionObj = Object.assign({},this.props.listItem,{question:event.currentTarget.value});
    this.props.updateQuestion(questionObj,index);
  }
  render() {
    return (
      <Row className="create-question-item-container ">
          <Row>
            <Input value={this.props.listItem.question} className="create-question-input " onChange={(e)=>this.updateQuestion(e)}/>
          </Row>
          <Row>
            <h5 style={{marginLeft:'20px',marginTop:'10px'}}>{this.props.listItem.option[0]}</h5>
          </Row>
          <Row>
            <Col span={1} offset={22}>
              <Icon type="copy" theme="filled" style={{color:'#13C64F'}} onClick={()=>this.props.copyQuestion(this.props.index)}/>
            </Col>
            <Col span={1}>
              <Icon type="delete" theme="filled" style={{color:'#13C64F'}} onClick={()=>this.props.deleteQuestion(this.props.index)}/>
            </Col>
          </Row>
      </Row>
    );
  }
}
const mapDispatchToProps = {
  updateQuestion,
  copyQuestion,
  deleteQuestion,
}
export default connect(null,mapDispatchToProps)(QuestionItem);
