import {UPDATE_QUESTIONS,UPDATE_QUESTION,DELETE_QUESTION,COPY_QUESTION} from '../actions/types';

const initialState = {
    questions: [],
}
const updateQuestion = (state,action) => {
  let {question,index} = action;
  let questions = [...state.questions];
  questions[index]=question

  return {...state,questions};
}
export default function(state = initialState, action){

    switch(action.type){
        case UPDATE_QUESTIONS:
        return Object.assign({}, state, {
                questions: action.questions
              })
        case UPDATE_QUESTION:
        return Object.assign({},updateQuestion(state,action))
        case DELETE_QUESTION :
        state.questions.splice(action.index,1)
        return Object.assign({}, state, {
                questions: [...state.questions]
              })
        case COPY_QUESTION:
        state.questions.push(state.questions[action.index])
        return Object.assign({}, state, {
                questions: [...state.questions]
              })
        return
        default:
            return state
    }

}
